body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.project-container {
    margin-top: -5px;
    margin-bottom: 1rem;
    max-width: 50rem;
    font-family: Calibri;
    border-style: solid;
    border-width: 2px;
    padding: 0.5em;
    border-radius: 1em;
    border: 0.1em solid #6c9dda;
    background-color: #fff;
    color: #18375e;
  }
  
  .project-title {
    display: inline-block;
    font-size: 1.5em;
    padding-left: 1em;
    color: #18375e;
  }
  
  .project-desc {
    display: inline;
    font-size: 20px;
  }
  
  .project-links {
      text-decoration: none;
      color: inherit;
  }
  
  .project-button {
      display: inline-block;
      font-size: 1em;
      border: none;
      background-color: #d3e7ff;
      border-radius: 1em;
      color:#18375e;
      padding: 0.5em;
      margin: 0.5em;
      transition-duration: 0.4s;
      cursor: pointer;
  }
  
  .project-button:hover {
      background-color: #6c9dda;
      cursor: pointer; 
  }

  .project-image {
    display: block;
    width: 100%;
    height: auto;
    max-width: 90%;
    max-height: 10em;
    border-radius: 1em;
    object-fit: cover;
    border: 0.2em solid #6c9dda;
    margin: 0.5em;
  }

  .centered {
    justify-content: center;
    display: flex;
  }
.back {
  background-image: linear-gradient(
    to top,
    #e6f4f1,
    #ecf7f8,
    #f3f9fc,
    #fafcfe,
    #ffffff
  );
}

.centered {
  display: flex;
  justify-content: center;
}

.container {
  margin: 1em;
  max-width: 39rem;
  font-family: Calibri;
  padding: 2em;
  border-radius: 1em;
  border: 0.1rem solid #6c9dda;
  background-color: #cbe2ff;
  color: #18375e;
}

h1,
h2,
p {
  margin-top: 0;
  margin-bottom: 0.5em;
  line-height: 28px;
  color: #18375e;
}

h2 {
  font-size: 30px;
}

h1 {
  font-size: 50px;
}

p,
li {
  font-size: 20px;
}

.sidenav {
  position: fixed;
  width: 13em;
  border: 0.1rem solid #6c9dda;
  margin: 1em;
  background-color: #cbe2ff;
  color: #18375e;
  border-radius: 1em;
  padding: 2em;
  margin-right: 48em;
}

.content {
  margin-left: 20em;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
  position: relative;
  display: inline;
}

.sidenav-link:before,
.sidenav-link:after {
  content: "";
  position: absolute;
  width: 0%;
  height: 3px;
  top: 50%;
  margin-top: 1px;
  background: #18375e;
}

.sidenav-link:before {
  left: -2.5px;
}
.sidenav-link:after {
  right: 2.5px;
  background: #18375e;
  transition: width 1.2s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.sidenav-link:hover:before {
  background: #18375e;
  width: 100%;
  transition: width 0.5s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.sidenav-link:hover:after {
  background: transparent;
  width: 100%;
}

.sidenav-link:hover:focus {
  background: transparent;
  width: 100%;
}

.button-no-style {
  background: inherit;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  text-align: inherit;
}

.inline-list > li,
.inline-list {
  display: inline;
  margin: 0 0.5em 0 0.5em;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

ul {
  -webkit-padding-start: 1em;
          padding-inline-start: 1em;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}

#collapse-items {
  display: inline;
}

#collapse-navbar {
  display: none;
}

.about-list {
  margin: 0;
  -webkit-padding-start: 2em;
          padding-inline-start: 2em;
}

.about-list > li {
  list-style: square;
}

@media screen and (min-width: 70.1em) {
  #collapse-items {
    display: inline !important;
  }
}

@media screen and (max-width: 70em) {
  .sidenav {
    position: fixed;
    width: auto;
    margin-right: 1em;
    max-height: 16.5em;
    min-width: 11em;
  }

  .content {
    margin-left: 0;
    margin-top: 16.5em;
  }

  ul {
    margin-top: 0;
  }

  h1 {
    justify-content: center;
    margin-bottom: 0;
  }

  .sidenav-section {
    display: block;
    margin-bottom: 0;
  }

  .sidenav-section:nth-child(2) {
    margin-top: 1em;
  }

  #collapse-navbar {
    display: inline;
  }

  #collapse-items {
    display: none;
  }
}

.about-image {
    width: 15em;
    height: 15em;
    border-radius: 100%;
    object-fit: cover;
    overflow: hidden;
    display: block;
}

.about-image-desc {
    display: block;
}

.centered {
    justify-content: center;
    display: flex;
}
